\r\n        display: inline-block;\r\n        font-size: 0;\r\n        height: 1px;\r\n        margin: 0!important;\r\n        overflow: hidden;\r\n        padding: 0!important;\r\n        position: absolute!important;\r\n        width: 1px;\r\n    }\r\n    ul {\r\n        display: flex;\r\n        list-style: none;\r\n        li {\r\n            a {\r\n                color: #fff;\r\n                padding: 10px 20px;\r\n                text-decoration: none;\r\n            }\r\n        } \r\n    }\r\n}\r\n\r\n// Main\r\nmain, header {\r\n    max-width: 1440px;\r\n    margin: 0 auto;\r\n}\r\n\r\n.slider {\r\n    height: 66vh;\r\n    display: grid;\r\n    grid-template-columns: repeat(7, 1fr) 200px repeat(4, 1fr);\r\n    grid-template-rows: repeat(6, 1fr);\r\n    .img_ctn {\r\n        grid-column: 1/8;\r\n        grid-row: 1/7;\r\n        .img {\r\n            width: 100%;\r\n            height: 100%;\r\n        }\r\n    }   \r\n    .txt_ctn {\r\n        grid-column: 8/13;\r\n        grid-row: 1/6;\r\n        margin: auto 0;\r\n    } \r\n    .slider_controls {\r\n        grid-column: 8/9;\r\n        grid-row: 6/7;\r\n        display: flex;\r\n        .slider_btn {\r\n            width: 100%;\r\n            height: 100%;\r\n            padding: 15px;\r\n            background-color: #000;\r\n            border: unset;\r\n        }\r\n    }\r\n}\r\n\r\n.about {\r\n    height: 34vh;\r\n    display: grid;\r\n    grid-template-columns: repeat(7, 1fr) 200px repeat(4, 1fr);\r\n    grid-template-rows: repeat(1, 1fr);\r\n    .dark_img_ctn, .light_img_ctn {\r\n        grid-column: 1/5;\r\n        grid-row: 1/2;\r\n        .img {\r\n            height: 100%;\r\n            width: 100%;\r\n        }\r\n    }\r\n    .light_img_ctn { \r\n        grid-column: 9/13;\r\n        grid-row: 1/2;\r\n        .img {\r\n            height: 100%;\r\n            width: 100%;\r\n        }\r\n    }\r\n    .txt_ctn {\r\n        grid-column: 5/10;\r\n        grid-row: 1/2;\r\n        margin: auto 0;\r\n    }\r\n}",null],"sourceRoot":""}]);
